.popup-container {
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 100vh; */
    background-color: transparent;
}

.add-transaction-form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-height: 90vh;
    border-radius: 15px;
    padding: 2%;
}

.add-tansaction-btn {
    background-color: #2d57ff;
    color: white;
    border-radius: 10px;
    padding: 10px 25px;
    font-size: 12px;
    outline: none;
    cursor: pointer;
    border-style: none;
}

.form-head {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.close-btn {
    background-color: transparent;
    border-style: none;
    outline: none;
    cursor: pointer;
}

.add-to-transactions {
    padding: 10px;
    background-color: #2d57ff;
    border-style: none;
    outline: none;
    cursor: pointer;
    color: #fff;
    border-radius: 10px;
}