.transaction-cotainer {
    width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
}
.transactions-section {
    width: 85%;
    display: flex;
    flex-direction: column;
}

.transactions-list-container {
    background-color: #e8f5fa;
    padding: 0px 2%;
    width: 100%;
    min-height: 87%;
}

.transaction-tabs {
    padding-left: 0px;
    display: flex;
    list-style: none;
    padding: 10px;
    font-weight: 600;
    column-gap: 2%;
}

.transactions-list {
    width: 100%;
    padding-left: 0px;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    border-radius: 15px;
    padding: 2%;
    text-align: left;
    
}

.arrow-icon {
    color: #718EBF;
    height: 100%;
    width: fit-content;
    padding-right: 10px;
}

.hr-el {
    border: 1px solid#E2E2E2;
}

.transaction-tab-btn {
    background-color: transparent;
    border-style: none;
    outline: none;
    cursor: pointer;
}